<template>
	<div>
			<!--商家开始-->
			<div class="place-rstbox clearfix">
				<div class="clearfix" style="height: 500px;" >
					<!--接受了父组件的值并且进行判断是否和父组件的传来的值是否相等-->
					<a href="#" class="rstblock" v-for="item in shopList" v-if="item.oneId==list[0] && item.twoId==list[1] ||list[1]==11" @click="goShopDetail(item)">
						<div class="rstblock-logo">
							<img :src="item.imgUrl" class="rstblock-logo-icon">
							<span>{{item.time}}</span>
							<div class="elemeicon elemeicon-premiumsign rstblock-logo-premiumsign" v-if="item.pinppai"></div>
						</div>
						<div class="rstblock-content">
							<div class="rstblock-title">{{item.title}}</div>
							<div class="starrating icon-star">
								<span class="icon-star"style="item.star" ></span>
							</div>
							<div class="rstblock-cost">{{item.fee}}</div>
							<div class="rstblock-schedule" v-for="subitem in item.yuList" >{{subitem}}</div>
							<div class="rstblock-activity">
								<i style="background:#fff;color:#999999;border:1px solid;padding:0;" v-for="subitem in item.baoList">{{subitem}}</i>
							</div>
						</div>
					</a>
					<router-view></router-view>
				</div>
			</div>
			<!--商家结束-->
	</div>
</template>
<script>
	export default{
		props:['list'],//接受父组件传来的值
		data(){
			return{
				shopList:[]
			}
		},
		methods:{
				getList(){
					this.shopList=JSON.parse(localStorage.getItem('shopList'))
				},
				goShopDetail(item){
					this.$router.push
					({name:'ShopDetail',params:{id:item.shopId}})
					localStorage.setItem('shopList',JSON.stringify(this.shopList));
				}
		},
		created(){
			this.getList()//调用方法取出存在本地的值
		}
		//晒选方法
	}
</script>
<style scoped>
  
</style>



