//自己定义的方法
const productListMock = function() {
  let data = [];
  /*let secondClassify1 = ['简餐便当','地方菜系','小吃炸串','面食粥点','香锅冒菜','汉堡披萨','日韩料理','轻食西餐'];*/
  let stars = ['width:96%','width:56%','width:76%','width:86%','width:66%','width:84%'];
  for (let i = 0; i < 6; i++) {
    let newData = {
      //classify: "美食",
     // secondClassify: Random.pick(secondClassify1),
     // userName:Random.cname(1,5),
     // phonenumber:Random.integer(1000000,10000000000),
      //adr:Random.word(10,20),
      id: i,
      name: Random.word(5, 10), //  
      description:Random.word(10,20),
      sales:Random.word(10,30),
      imgUrl: Random.dataImage('100x100', '图片'),
      time: Random.integer(15,50),
      grade:Random.integer(1,200),
      price:Random.integer(10,100),
      star: Random.pick(stars),
      count: 1
      /*fee: Random.integer(3,10),
      piao: Random.boolean(),
      bao: Random.boolean(),
      xin: Random.boolean(),
      introduction: Random.paragraph(1, 3),*/
    }
    data.push(newData)
  }
  return {
    productList: data,
    code: true
  }
}
Mock.mock('/data_from_mock','post',productListMock);



