/*
  这里面定义所有页面需要的展示数据，具体的内容根据具体组件要求来定义
 */
export default { 
	//返回购物车
  GetShopCar: (state) => {
    return state.shopCar;
  },
  //返回总价格
  GetTotalPrice:(state) =>{
  	return state.totalPrice;
  },
  //返回总数
  GetTotalCount:(state) =>{
  	return state.totalCount;
  }
}



