
<template>
	<!--首页内容-->
	<div class="ng-scope">
		<!--地址搜索框-->
		<search></search>
		<!--谁去拿外卖-->
		<who-takeout></who-takeout>
		<!--商品-->
		<div class="container ng-scope">
			<!--商家分类开始（接受shopclass传来的参数，接受子组件传来的值）-->
		  <shop-class v-on:showId="getId"></shop-class>
			<!--商家分类结束-->
			<!--商家列表开始 （给子组件传值）-->
			<shop-list v-bind:list="idList"></shop-list>
			<!--商家结束-->
		</div>
	</div>
</template>
<script>
import Search from '@/components/homeContent/search/Search.vue'
import WhoTakeout from '@/components/homeContent/who_Takeout/WhoTakeoutDiv.vue'
import ShopClass from '@/components/homeContent/shopClass/ShopClass.vue'
import ShopList from '@/components/homeContent/shopList/ShopList.vue'
export default{
	components:{
		//注册组件，谁去拿外卖组件
		WhoTakeout:WhoTakeout,
		ShopClass:ShopClass,
		ShopList:ShopList,
		Search:Search  
  },
  data(){
  	return{
  		idList:[]//定义数组接受shopclass传来的数组里面的值
  	}
  },
  methods:{
  	getId(val){
  		this.idList = val;//接受shopclass传来的数组里面的值
  	}
  }
}
</script>
<style scoped>
  
</style>


