<template>
    <!--购物车-->
  <div id="shopCar">
    <div cart-link="cartLink" class="ng-isolate-scope" ng-hide="shopCache.id == 656683" shop-cart="">
        <div class="shop-cart">
            <!--购物车头部-->
            <div class="shop-cartbasket" id="shopbasket" style="top: -312px; height: auto;">
                <div cart="shopCart" class="ng-isolate-scope" shop-groupswitcher="">
                    <div class="shop-grouphead single" ng-class="{ single: shopCart.vm.groups.length === 1 }">
                        <a class="icon-cart-add ng-scope" href="javascript:" ng-click="addGroup()" ng-if="shopCart.vm.groups.length === 1" tooltip="添加购物车">
                        </a>
                        <div class="shop-grouphead-row">
                            购物车
                            <a href="javascript:" ng-click="shopCart.clearGroup(shopCart.currentGroupIndex)" @click="clearShopCars">
                                [清空]
                            </a>
                        </div>
                    </div>
                </div>
                <!--加入购物车-->
                <div class="shopcar">
                    <div ng-repeat="item in shopCart.vm.groups[shopCart.currentGroupIndex]" class="shop-cartbasket-tablerow ng-scope" entityid="1363707300" v-for="(product,index) in GetShopCar" >
                      <div class="cell itemname ng-binding" ng-bind="item.name">
                        {{product.name}}
                      </div>
                      <div class="cell itemquantity">
                        <button ng-click="shopCart.subEntity(item, $event)" @click="addProductCount(index)">-</button>
                        <input v-model="product.count" ng-blur="shopCart.updateFromInput(item, item.quantity)" class="ng-pristine ng-valid">
                        <button ng-click="shopCart.addEntity(item, $event)">+</button>
                      </div>
                      <div class="cell itemtotal ng-binding" ng-bind="'¥' + ((item.price * item.quantity).toFixed(2) | number)">
                      ¥{{product.price}}
                      </div>
                    </div>
                </div>
            </div>
            <!--购物车底部-->
            <div class="shop-cartfooter" ng-click="shopCart.toggleCart()">
                <span class="icon-cart shop-carticon">
                    <!-- ngIf: shopCart.vm.quantity > 0 -->
                    <span class="shop-cartpieces ng-binding ng-scope" ng-bind="shopCart.vm.quantity" ng-if="shopCart.vm.quantity > 0">
                    {{GetTotalCount}}
                    </span>
                    <!-- end ngIf: shopCart.vm.quantity > 0 -->
                </span>
                <!-- ngIf: shopCart.vm.quantity > 0 -->
                <p class="shop-cartfooter-text price ng-binding ng-scope" ng-bind="shopCart.vm.total | number" ng-if="shopCart.vm.quantity > 0">
                 {{GetTotalPrice}}
                </p>
                <!-- end ngIf: shopCart.vm.quantity > 0 -->
                <!-- ngIf: shopCart.vm.extras.length && !shopCart.vm.button.disabled -->
                <div class="shop-cartfooter-text extras ng-binding" ng-bind-html="shopCart.vm.picewiseText">
                    
                </div>
                <Button class="shop-cartfooter-checkout ng-binding" type="primary" @click="modal1 = true">去结算</Button>
               <!--  <button class="shop-cartfooter-checkout ng-binding" ng-bind="shopCart.vm.button.text" ng-class="{disabled: shopCart.vm.button.name !== 'CAN_ORDER'}" ng-click="checkout($event)" ng-disabled="shopCart.vm.button.disabled" @click="saveShopCar(shopCarCount,shopCarTotal)">
                   去结算 >
               </button> -->
            </div>
        </div>
    </div>
     <!-- iview使用 -->
    <Modal
        v-model="modal1"
        title="欢迎来到饿了么，请填写你的基本信息"
        @on-ok="ok"
        @on-cancel="cancel">
        <label class="popopUp">姓名：<input type="text" name="姓名" v-model:value="userName"></label>
        <label class="popopUp">电话：<input type="text" name="姓名 "  v-model:value="phonenumber"></label>
        <label class="popopUp">地址：<input type="text" name="姓名"  v-model:value="adr"></label>
    </Modal>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'
  import '@/store/state'
  export default{
    data(){
        return{
          modal1:false,
          users:'',//用户对象
          userName:null,//姓名
          phonenumber:null,//电话
          adr:null,//地址
          usersData:''
        }
    },
    methods:{
            ok () {
              //点击确定按钮后跳到订单页            
              this.$router.push({name:"Order"})
              //接受用户信息对象(JSON.stringify是从一个对象中解析出字符串)
              this.users={
                userName:this.userName,
                phonenumber:this.phonenumber,
                adr:this.adr
              }
              //存用户信息对象(JSON.stringify是从一个对象中解析出字符串)
              localStorage.setItem("User", JSON.stringify(this.users))
            },
            //增加数量
            addProductCount(product,index){
                    if(product.id===this.$store.GetShopCar[index].id){
                        product.count+=1
                        this.$store.dispatch('addShopCarCount',product.count)
                        return
                    }
              
            },
            //点击取消按钮
            cancel () {
                this.$Message.info('您取消了所填写的信息');
            },
            //清空购物车
            clearShopCars(){
           this.$store.dispatch('clearShopCar')
           localStorage.setItem("User", JSON.stringify(this.usersData))
          }
        },
          //从vuex里面的取出数据
           computed:{...mapGetters(['GetShopCar','GetTotalPrice','GetTotalCount'])}
      }
            /* //增加一个产品
            addProduct(product){
              for (var i = this.$store.state.shopCar.length - 1; i >= 0; i--) {
                  if (this.$store.state.shopCar[i].id===product.id) {
                    this.$store.dispatch('addPrice',product.price)
                    this.$store.dispatch('addCount',i)
                    return
                  }
              }
              this.$store.dispatch('addProduct',product)
              this.$store.dispatch('addPrice',product.price)  
              return
            },*/
           /* //减少一个
            removeProduct(product){
              for (var i = this.$store.state.shopCar.length - 1; i >= 0; i--) {
                  if (this.$store.state.shopCar[i].id===product.id) {
                    if (this.$store.state.shopCar[i].count>1) {
                      this.$store.dispatch('lessPrice',product.price)
                      this.$store.dispatch('lessCount',i)
                      return
                    }else {
                      this.$store.dispatch('removeProduct',i)
                      this.$store.dispatch('lessPrice',product.price) 
                      return
                    }
                }
              } 
            }*/
        
          //取出vuex里面的state里的数据
</script>
<style>
  .shopcar{
    background-color: #fff;
    height: 270px;
  }
  .popopUp{
    text-align: center;
    display: block;
    margin-bottom: 10px;
  }
</style>


