/*
  这里面只用于对state的操作，不允许进行逻辑、流程、异步等操作
 */
import {
  ADDPRODUCT,
  REMOVEPRODUCT,
  ADDPRICE,
  ADDCOUNT,
  LESSPRICE,
  LESSCOUNT,
  CLEARSHOPCAR
} from './types'

export default {
  /*加入购物车*/
  [ADDPRODUCT]: (state,product) => {
     //把商品添加到购物车
     state.shopCar.push(product);
  },
  /*移除购物车
  [REMOVEPRODUCT]: (state,index) => {
     //把商品移除购物车
     state.shopCar.splice(index,1);
  },*/
  //增加价格
  [ADDPRICE]:(state,price) => {
     state.totalPrice+=price;
     state.totalCount+=1;
  },
  //增加单个商品数量
  [ADDCOUNT]:(state,index) => {
     state.shopCar[index].count+=1;
  },
  //减少价格
  [LESSPRICE]:(state,price) => {
     state.totalPrice=state.totalPrice-price;
     state.totalCount=state.totalCount-1;
  },
  //减少单个商品数量
  [LESSCOUNT]:(state,index) => {
     state.shopCar[index].count=state.shopCar[index].count-1;
  },
  //清空购物车
  [CLEARSHOPCAR]:(state) => {
    state.shopCar.splice(0,state.shopCar.length);//(项目位置，项目数量)
    state.totalPrice=0;
    state.totalCount=0;
    localStorage.setItem("shopCar", JSON.stringify(state.shopCar))
    localStorage.setItem("totalPrice", state.totalPrice)
    localStorage.setItem("totalCount", state.totalCount)
  }
}



