<template>
	<!--谁去拿外卖-->
		<div class="place-tab clearfix container ng-scope">
			<div class="place-fetchtakeout ng-isolate-scope " v-on:click="showDiv">
				<img src="https://shadowshadow.elemecdn.com/faas/desktop/media/img/takeout.408a87.png" alt="谁去拿外卖">
			</div>
			<!--遮罩-->
			<div style="position: fixed; left: 0px; top: 0px; width: 100%; height: 100%; opacity: 0.5; background: rgb(0, 0, 0); z-index: 500;" v-if="takeoutShow"></div>
			<whotakeout @clickhidePanel="hideDiv" v-if="takeoutShow"></whotakeout>
			<!-- <popup :panelShow.sync="panelShow"></popup> 双向绑定-->
		</div>
</template>
<script>
	import whotakeout from '@/components/homeContent/who_Takeout/WhoTakeout.vue'
	export default{
		components:{
			whotakeout:whotakeout
		},
		data(){
			return{
				//谁去拿外卖默认不显示
				takeoutShow:false,
			}
		},
		methods:{
		  	//谁去拿外卖
		  	showDiv(){
		  		this.takeoutShow=true;
		  	},
		  	hideDiv(){
		  		this.takeoutShow=false;
		  	},
		}
}
</script>
<style scoped>
  
</style>


