<template>
	<!--商家详细信息-->
  <div class="shopguide ng-isolate-scope" data="shop" isdisabled="shop.disabled" shop-guide="">
      <div class="container" itemscope="" itemtype="http://schema.org/Restaurant" ng-show="shop">
          <div class="shopguide-info">
              <meta content="https://www.ele.me/shop/159228632" itemprop="url">
                  <img alt="乡村基(环球中心店)" itemprop="image" ng-src="https://fuss10.elemecdn.com/9/47/39f3c04df7f5db9edb64871d127b1png.png?imageMogr2/thumbnail/95x95/format/webp/quality/85" src="https://fuss10.elemecdn.com/9/47/39f3c04df7f5db9edb64871d127b1png.png?imageMogr2/thumbnail/95x95/format/webp/quality/85">
                      <div class="shopguide-info-wrapper">
                          <div>
                              <h1 class="ng-binding" itemprop="name" ng-class="{hastip: shop.tip}" title="乡村基(环球中心店)">
                                  乡村基(环球中心店)
                              </h1>
                              <!-- ngIf: shop.tip -->
                              <a href="/shop/159228632/info" ng-href="/shop/159228632/info">
                                  <!-- ngIf: isJingHua -->
                              </a>
                          </div>
                          <p class="shopguide-info-rate">
                              <div class="starrating icon-star ng-isolate-scope" rate-star="" rating="shop.rating" title="评分5.0分">
                                  <span class="icon-star" ng-style="{ width: (rating * 20) + '%' }" style="width: 100%;">
                                  </span>
                              </div>
                              (
                              <a class="ng-binding" href="/shop/159228632/rate" ng-href="/shop/159228632/rate">
                                  897
                              </a>
                              )
                          </p>
                          <p>
                              <!-- ngRepeat: flavor in shop.flavor -->
                          </p>
                      </div>
                      <div class="shopguide-info-extra">
                          <ul>
                              <!-- ngIf: shopRatingScore -->
                              <li class="shopguide-extra-item shopguide-extra-compete ng-scope" ng-if="shopRatingScore">
                                  <div itemprop="aggregateRating" itemscope="" itemtype="http://schema.org/AggregateRating">
                                      <h2 class="color-stress ng-binding" itemprop="ratingValue">
                                          5.0
                                      </h2>
                                      <meta content="5" itemprop="bestRating">
                                          <meta content="897" itemprop="reviewCount">
                                              <p>
                                                  综合评价
                                                  <br>
                                                      <span class="color-mute ng-binding">
                                                          高于周边商家
                                                      </span>
                                                      <!-- ngIf: shopRatingScore.compare_rating -->
                                                      <span class="color-stress ng-binding ng-scope" ng-if="shopRatingScore.compare_rating">
                                                          50.0%
                                                      </span>
                                                      <!-- end ngIf: shopRatingScore.compare_rating -->
                                                  </br>
                                              </p>
                                          </meta>
                                      </meta>
                                  </div>
                                  <div>
                                      <p>
                                          服务态度
                                          <div class="starrating icon-star ng-isolate-scope" rate-star="" rating="shopRatingScore.service_score" title="评分5.0分">
                                              <span class="icon-star" ng-style="{ width: (rating * 20) + '%' }" style="width: 100%;">
                                              </span>
                                          </div>
                                          <span class="color-stress ng-binding">
                                              5.0分
                                          </span>
                                      </p>
                                      <p>
                                          菜品评价
                                          <div class="starrating icon-star ng-isolate-scope" rate-star="" rating="shopRatingScore.food_score" title="评分4.7分">
                                              <span class="icon-star" ng-style="{ width: (rating * 20) + '%' }" style="width: 93.6612%;">
                                              </span>
                                          </div>
                                          <span class="color-stress ng-binding">
                                              4.7分
                                          </span>
                                      </p>
                                  </div>
                              </li>
                              <!-- end ngIf: shopRatingScore -->
                              <!-- ngIf: shop.description -->
                              <li class="shopguide-extra-item address">
                                  <p itemprop="streetAddress" itemscope="" itemtype="http://schema.org/PostalAddress">
                                      <span class="label">
                                          商家地址：
                                      </span>
                                      <span class="ng-binding">
                                          四川省成都市高新区天府大道北段1700号B1C27
                                      </span>
                                      <meta content="18227281940 13880752072" itemprop="telephone"/>
                                  </p>
                                  <p>
                                      <span class="label">
                                          营业时间：
                                      </span>
                                      <span class="ng-binding" itemprop="openingHours">
                                          10:00-20:40
                                      </span>
                                  </p>
                              </li>
                              <li class="shopguide-extra-item">
                                  <p class="shopguide-extra-delivery">
                                      由
                                      <span class="ng-binding">
                                          乡村基(环球中心店)
                                      </span>
                                      提供配送服务
                                  </p>
                              </li>
                          </ul>
                      </div>
                  </img>
              </meta>
          </div>
          <div class="shopguide-server">
              <span class="" ng-hide="shop.id == 656683">
                  <em>
                      起送价
                  </em>
                  <em class="shopguide-server-value ng-binding">
                      0元
                  </em>
              </span>
              <span class="" ng-hide="shop.id == 656683">
                  <em>
                      配送费
                  </em>
                  <em class="shopguide-server-value ng-binding">
                      配送费¥4
                  </em>
                  <!-- ngIf: shop.delivery_mode.description -->
              </span>
              <span class="" ng-hide="shop.id == 656683">
                  <em>
                      平均送达速度
                  </em>
                  <em class="shopguide-server-value ng-binding">
                      20分钟
                  </em>
              </span>
          </div>
          <a class="shopguide-favor" href="javascript:" ng-click="favor()">
              <!-- ngIf: isFavorShop -->
              <!-- ngIf: !isFavorShop -->
              <i class="icon-favorite ng-scope" ng-if="!isFavorShop">
              </i>
              <!-- end ngIf: !isFavorShop -->
              <!-- ngIf: isFavorShop -->
              <!-- ngIf: !isFavorShop -->
              <span class="ng-scope" ng-if="!isFavorShop">
                  收藏
              </span>
              <!-- end ngIf: !isFavorShop -->
          </a>
      </div>
  </div>
</template>
<script>
  export default{
    data(){
      return{
        getId:0
      }
    },
    methods:{
      getParams(){
        let routerParams =this.$route.params.id
      }
    }
  }
</script>



