<template>
   <div class="shopmenu ng-isolate-scope" display-type="shop.displayType" filter-data="shop.filter" ng-if="shopAction === 'menu'" perf-click="desktop/201" shop-cache="shopCache" shop-menu="">
            <!--导航栏-->
            <div class="shopmenu-nav ng-isolate-scope" ng-show="!loading && !searchEnv" sticky="" sticky-body-class="shopmenu-nav-sticky" sticky-class="sticky" sticky-fn="shopNavSticky">
                <!-- ngRepeat: category in categorys -->
                <a class="ng-binding ng-scope active" href="javascript:" ng-class="{active: currentCategory.id === category.id}" ng-click="scrollToCategory(category)" ng-repeat="category in categorys">
                    热销
                </a>
                <!-- end ngRepeat: category in categorys -->
                <a class="ng-binding ng-scope" href="javascript:" ng-class="{active: currentCategory.id === category.id}" ng-click="scrollToCategory(category)" ng-repeat="category in categorys">
                    优惠
                </a>
                <!-- end ngRepeat: category in categorys -->
                <a class="ng-binding ng-scope" href="javascript:" ng-class="{active: currentCategory.id === category.id}" ng-click="scrollToCategory(category)" ng-repeat="category in categorys">
                    美味盖饭
                </a>
                <!-- end ngRepeat: category in categorys -->
                <a class="ng-binding ng-scope" href="javascript:" ng-class="{active: currentCategory.id === category.id}" ng-click="scrollToCategory(category)" ng-repeat="category in categorys">
                    千万销量
                </a>
                <!-- end ngRepeat: category in categorys -->
                <a class="ng-binding ng-scope" href="javascript:" ng-class="{active: currentCategory.id === category.id}" ng-click="scrollToCategory(category)" ng-repeat="category in categorys">
                    满减专区
                </a>
                <!-- end ngRepeat: category in categorys -->
                <a class="ng-binding ng-scope" href="javascript:" ng-class="{active: currentCategory.id === category.id}" ng-click="scrollToCategory(category)" ng-repeat="category in categorys">
                    单品售卖
                </a>
                <!-- end ngRepeat: category in categorys -->
                <a class="ng-binding ng-scope" href="javascript:" ng-class="{active: currentCategory.id === category.id}" ng-click="scrollToCategory(category)" ng-repeat="category in categorys">
                    凑单神器
                </a>
                <!-- end ngRepeat: category in categorys -->
            </div>
             <!--内容-->
            <div ng-show="!loading" class="shopmenu-main grid" ng-class="{grid: displayType === 'grid', list: displayType === 'list'}">
                <div class="ng-scope" ng-if="filterData === 'default' && !searchEnv">
                    <div class="shopmenu-list clearfix ng-scope" ng-repeat="category in categorys">
                        <h3 class="shopmenu-title ng-binding">
                            热销
                            <span class="shopmenu-des ng-binding">
                                大家喜欢吃，才叫真好吃。
                            </span>
                        </h3>
                        <!--1-->
                        <div class="shopmenu-food ng-isolate-scope" food="food" id="1383916130" ng-class="{noimg: !food.image_path}" ng-repeat="food in category.foods" shop="shopCache" shop-menu-item=""  v-for="product in productLists"> 
                            <!-- ngIf: food.image_path -->
                            <span class="col-1 ng-scope" ng-if="food.image_path">
                                <a href="javascript:" ng-click="showInfo(food)">
                                    <img alt="川式小煎鸡盖饭的图片" :src="product.imgUrl"/>
                                </a>
                            </span>
                            <!-- end ngIf: food.image_path -->
                            <div class="col-2 shopmenu-food-main">
                                <h3 class="shopmenu-food-name ui-ellipsis ng-binding">
                                    {{product.name}}
                                </h3>
                                <p class="color-mute ui-ellipsis ng-binding" tooltip="鸡肉丁、莲藕丁、香菇丁、自家配方，炒制香浓的汤汁盖上晶莹的米饭，这味倍儿爽！">
                                   {{product.description}}
                                </p>
                                <p>
                                    <div class="starrating icon-star ng-isolate-scope" rate-star="" rating="food.rating" title="评分4.2分">
                                        <span class="icon-star" ng-style="{ width: (rating * 20) + '%' }" :style="product.star">
                                        </span>
                                    </div>
                                    <span class="color-mute ng-binding">
                                        {{product.grade}}
                                    </span>
                                    <span class="color-mute ng-binding">
                                        {{product.sales}}
                                    </span>
                                </p>
                            </div>
                            <span class="col-3 shopmenu-food-price color-stress ng-binding">
                                {{product.price}}
                                <small class="ng-binding">
                                </small>
                                <!-- ngIf: food.min_purchase && food.min_purchase > 1 -->
                            </span>
                            <span class="col-4">
                                <div class="ng-isolate-scope" food="food" ng-hide="shop.id == '656683'" shop-cartbutton="">
                                    <!-- ngIf: !menuFood.hasSpec -->
                                    <div class="ng-scope" ng-if="!menuFood.hasSpec">
                                        <!-- ngIf: !cartItem.quantity && menuFood.stock -->
                                        <button class="shop-cartbutton ng-binding ng-scope" ng-click="cartItem.add($event)" ng-if="!cartItem.quantity && menuFood.stock" @click="addProduct(product)" >
                                            加入购物车
                                        </button>
                                    </div>
                                </div>
                            </span>
                        </div>
                         <!--商品介绍-->
                    </div>
                    <!--内容结束-->
                </div>
            </div>
   </div>
</template>
