  import {mapGetters} from 'vuex'
  import '@/store/state'
  export default{
    data(){
      return{
        //定义一个空的数组，存储从mock传来的数组
        productLists:[
                    {
                    id:1,
                    name:'abc',
                    description:'abcdegfg',
                    sales:'20元',
                    grade:100,
                    price:35,
                    imgUrl:'https://fuss10.elemecdn.com/c/45/df80f9d5a819dc3d46a2bb8b59577png.png?imageMogr2/thumbnail/70x70/format/webp/quality/85',
                    star:'width:96%'
                    },
                     {
                    id:1,
                    name:'abc',
                    description:'abcdegfg',
                    sales:'20元',
                    grade:100,
                    price:35,
                    imgUrl:'https://fuss10.elemecdn.com/c/45/df80f9d5a819dc3d46a2bb8b59577png.png?imageMogr2/thumbnail/70x70/format/webp/quality/85',
                    star:'width:96%'
                    },
                     {
                    id:1,
                    name:'abc',
                    description:'abcdegfg',
                    sales:'20元',
                    grade:100,
                    price:35,
                    imgUrl:'https://fuss10.elemecdn.com/c/45/df80f9d5a819dc3d46a2bb8b59577png.png?imageMogr2/thumbnail/70x70/format/webp/quality/85',
                    stars:'width:96%'
                    },
                     {
                    id:1,
                    name:'abc',
                    description:'abcdegfg',
                    sales:'20元',
                    grade:100,
                    price:35,
                    imgUrl:'https://fuss10.elemecdn.com/c/45/df80f9d5a819dc3d46a2bb8b59577png.png?imageMogr2/thumbnail/70x70/format/webp/quality/85',
                    stars:'width:96%'
                    },
                     {
                    id:1,
                    name:'abc',
                    description:'abcdegfg',
