                    grade:100,
                    price:35,
                    imgUrl:'https://fuss10.elemecdn.com/c/45/df80f9d5a819dc3d46a2bb8b59577png.png?imageMogr2/thumbnail/70x70/format/webp/quality/85',
                    stars:'width:96%'
                    },
                     {
                    id:1,
                    name:'abc',
                    description:'abcdegfg',
                    sales:'20元',
                    grade:100,
                    price:35,
                    imgUrl:'https://fuss10.elemecdn.com/c/45/df80f9d5a819dc3d46a2bb8b59577png.png?imageMogr2/thumbnail/70x70/format/webp/quality/85',
                    stars:'width:96%'
                    }
        ]
      }
    },
    computed:{
    },
    methods:{
       //添加商品信息
      addProduct(product){
          for (var i = this.$store.state.shopCar.length - 1; i >= 0; i--) {
              if (this.$store.state.shopCar[i].id===product.id) {
                //调用添加价格
                this.$store.dispatch('addPrice',product.price)
                //调用添加id
                this.$store.dispatch('addCount',i)
                //调用写数据到本地方
                this.$store.dispatch('writeDataToLocal')
                return
              }
          }
          //调用将商品信息放入vuex里面
          this.$store.dispatch('addProduct',product)
          //调用添加价格
          this.$store.dispatch('addPrice',product.price) 
          //调用写数据到本地方 
          this.$store.dispatch('writeDataToLocal')
      }
    },
  
      //移除商品信息
     /* removeProduct(product){
          for (var i = this.$store.state.shopCar.length - 1; i >= 0; i--) {
              if (this.$store.state.shopCar[i].id===product.id) {
                if (this.$store.state.shopCar[i].count>1) {
                  this.$store.dispatch('lessPrice',product.price)
                  this.$store.dispatch('lessCount',i)
                }else {
                  this.$store.dispatch('removeProduct',product)
                  this.$store.dispatch('lessPrice',product.price) 
                }
                this.$store.dispatch('writeDataToLocal')
              }
          } 
        }*/
     //接受从mock传来的数据
 /*  created(){
        this.$http.post('/data_from_mock').then((res) =>{
            this.productList = res.data.productList;
            console.log(JSON.stringify(this.productList));
        }) */
        //从本地加载数据
        /*this.$store.dispatch('loadDataFromLocal')*/
   // }
}
</script>
