<template>
	<div id="whotakeout">
		<div class="fetchtakeout-dialog ng-scope" style="left: 503px; top: 69.5px; z-index: 1001;">
			<div class="fetchtakeout-dialog-header">
				<a href="JavaScript:" class="fetchtakeout-close icon-close" @click="hideDiv"></a>
				<h3>谁去拿外卖</h3>
		  </div>
			<div class="fetchtakeout-dialog-body">
				<div class="fetchtakeout-wrapper">
					<h2 class="fetchtakeout-badge"></h2>
					<button class="fetchtakeout-btn"  ng-class="{ active: btnActive }" v-on:click="showRandom"></button>
					<div class="fetchtakeout-rules">随机到最小数字的人去拿外卖</div>
					<div class="fetchtakeout-emptytext" ng-show="diceList.length === 0" v-if="showStart">↓ Start</div>
					<ul class="fetchtakeout-list">
						<!-- 生成随机数 -->
						<li v-for="(item,index) in liArray" v-if="index<10" class="ng-binding ng-scope" v-bind:class="{ 'selected': item === minNum}" >
							扔出了一个{{item}}
						</li>
					</ul>
				</div>
			</div>
	 </div>
	</div>
</template>
<script>
	export default{
		name:'whotakeout',
		data(){
			return{
			showStart:true,
			liArray:[],//随机数组
			minNum:0//最小数
		 	}
		},
		methods: {
			hideDiv () {
				this.$emit('clickhidePanel')
			},
			showRandom () {
		 	this.showStart=false
		 	this.liArray.push(parseInt(100*Math.random()+1))//接受随机数
		 	this.minNum=Math.min.apply(Math,this.liArray)
		 },

		}
	}
</script>
<style scoped>
.fetchtakeout-list li{
		text-align: left;
}
</style>


