
<template>
	<!--所有商品，搜素框-->
  <div class="ng-scope ng-isolate-scope" data="shop" display-type="shop.displayType" filter-data="shop.filter" shop-action="shopAction" shop-nav="">
      <div class="shopnav">
          <div class="container clearfix">
            <!-- 左侧点击 -->
              <div class="shopnav-left">
                  <router-link to="shoperHome"><a class="shopnav-tab active" href="/shop/159228632" ng-class="{active: shop.tab === 'index'}">
                      所有商品
                  </a></router-link>
                  <router-link to="comments"><a class="shopnav-tab" href="/shop/159228632/rate" ng-class="{active: shop.tab === 'rate'}">
                      评价
                  </a></router-link>
                  <router-link to="info"><a class="shopnav-tab" href="/shop/159228632/info" ng-class="{active: shop.tab === 'info'}">
                      商家资质
                  </a></router-link>
                  <!-- ngIf: shopAction === 'menu' -->
                  <!-- 显示隐藏默认排序评分，销量，价格 -->
                  <span class="shopnav-filter ng-scope" ng-if="shopAction === 'menu'" v-if="showsearch">
                      <a class="active" href="javascript:" ng-class="{active: filterData === 'default'}" ng-click="filter('default')">
                          默认排序
                      </a>
                      <a href="javascript:" ng-class="{active: filterData === '-rating'}" ng-click="filter('-rating')">
                          评分
                          <i class="icon-uniE02D">
                          </i>
                      </a>
                      <a href="javascript:" ng-class="{active: filterData === '-month_sales'}" ng-click="filter('-month_sales')">
                          销量
                          <i class="icon-uniE02D">
                          </i>
                      </a>
                      <a href="javascript:" ng-class="{active: filterData === '-price' || filterData === 'price'}" ng-click="filter('-price')">
                          价格
                          <i class="icon-uniE02D" ng-hide="filterData === 'price'">
                          </i>
                          <i class="icon-uniE02C ng-hide" ng-show="filterData === 'price'">
                          </i>
                      </a>
                      <span>
                          <a class="shopnav-filter-display icon-grid active" href="javascript:" ng-class="{active: displayType === 'grid'}" ng-click="changeDisplayType('grid')" title="以网格形式显示菜单">
                          </a>
                          <a class="shopnav-filter-display icon-list" href="javascript:" ng-class="{active: displayType === 'list'}" ng-click="changeDisplayType('list')" title="以列表形式显示菜单">
                          </a>
                      </span>
                  </span>
                  <!-- end ngIf: shopAction === 'menu' -->
              </div>
              <!-- 搜素框 -->
              <div class="shopnav-search">
                  <form class="place-search ng-pristine ng-valid" ng-submit="search()" role="search">
                      <button class="place-search-btn icon-search" title="搜索商家或美食" ubt-data-keyword="">
                      </button>
                      <label for="globalsearch">
                          搜索商家或美食
                      </label>
                      <input autocomplete="" class="place-search-input ng-pristine ng-valid" id="globalsearch" ng-model="searchText" placeholder="搜索商家,美食..."/>
                  </form>
              </div>
          </div>
      </div>
  </div>
</template>
<script>
  export default{
    data(){
      return{
        showsearch:true
      }
    },
    methods:{
      showSearch(){
        this.showsearch=false
      }
    }
  }
</script>
<style scoped>
  
</style>


