import Vue from 'vue'
import Router from 'vue-router'
/*import HelloWorld from '@/components/HelloWorld'*/
import HomeContent from '@/components/homeContent/HomeContent'
import SideBar from '@/components/sideBar/SideBar.vue'
import Order from '@/components/order/Order'
/*商家*/
import ShopDetail from '@/components/homeContent/shopDetail/ShopDetail'
import ShoperHome from '@/components/homeContent/shopDetail/shoperHome/ShoperHome'
import ShoperComments from '@/components/homeContent/shopDetail/shoperComments/ShoperComments'
import ShoperInfo from '@/components/homeContent/shopDetail/shoperInfo/ShoperInfo'
Vue.use(Router)
export default new Router({
  mode:'history',
  routes: [
   /* {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },*/
    {
      path: '/',//主页内容
      name: 'HomeContent',
      /*命名路由*/
      components:{
        default:HomeContent,
        cart:SideBar
      }
    },
    {
      path:'/order',
      name:'Order',
      component:Order
    },
    {
      path:'/shopdetail/:id',
      name:'ShopDetail',
      component:ShopDetail,
      redirect: {name:'ShoperHome'},
      children:[
        {
        path: 'shoperHome', //商家主页
        name: 'ShoperHome',
        component: ShoperHome
        },
        {
          path: 'comments', //商家评价
          name: 'ShoperComments',
          component: ShoperComments
        },
        {
          path: 'info', //商家信息
          name: 'ShoperInfo',
          component: ShoperInfo
        }
      ]
    }
  ]
})





