<template>
	<!--订单页-->
	<div id='Order'>
		<div ng-view="" role="main" class="ng-scope">
			<div class="profile-container container" profile-container="" page-name="order" page-title="近三个月订单">
					<div class="clearfix">
						<div class="location" ng-style="{visibility: geohash ? '' : 'hidden'}" role="navigation" location="">
							<span>当前位置:</span> 
							<span class="location-current">
								<a class="inherit ng-binding" ng-href="/place/wm6jbhbeufzu" ubt-click="401" ng-bind="place.name || place.address" href="/place/wm6jbhbeufzu">新世纪环球购物中心成都环球中心天堂洲际大饭店
								</a>
							</span> 
							<span class="location-change" ng-class="{ 'location-hashistory': user.username &amp;&amp; userPlaces &amp;&amp; userPlaces.length > 0 }">
								<a ng-href="/home" ubt-click="400" hardjump="" href="/home">[切换地址]</a>
								<ul class="dropbox location-dropbox" ubt-visit="398">
									<li class="arrow"></li>
									<!-- ngRepeat: userPlace in userPlaces | filter:filterPlace | limitTo: 4 -->
									<li class="changelocation">
										<a ng-href="/home" hardjump="" href="/home">
											修改收货地址
											<span class="icon-location"></span>
										</a>
									</li>
								</ul>
							</span> 
							<span ng-transclude="">
								<i class="icon-arrow-right ng-scope"></i>
								<span class="ng-binding ng-scope">近三个月订单</span>
							</span>
						</div>
						<div search-input=""></div>
					</div>
					<ul class="profile-sidebar" role="navigation" profile-sidebar="">
						<li class="profile-sidebar-section">
							<h2 class="profile-sidebar-sectiontitle" ng-class="{ active: pageName === 'profile' }">
								<i class="icon-line-home"></i>
								<a href="/profile">个人中心</a>
							</h2>
						</li>
						<li class="profile-sidebar-section">
							<h2 class="profile-sidebar-sectiontitle">
								<i class="icon-line-order"></i>
								我的订单
							</h2>
							<ul>
								<li ng-class="{ active: pageName === 'order' }" class="active">
									<a href="/profile/order">近三个月订单</a>
								</li>
							</ul>
						</li>
						<li class="profile-sidebar-section">
							<h2 class="profile-sidebar-sectiontitle"><i
								class="icon-yen"></i>
								我的资产
							</h2>
							<ul>
								<li ng-class="{ active: pageName === 'hongbao' }">
									<a href="/profile/hongbao">我的红包</a>
								</li>
								<li ng-class="{ active: pageName === 'balance' }">
									<a href="/profile/balance">账户余额</a>
								</li>
								<li ng-class="{ active: pageName === 'points' }">
									<a href="/profile/points">我的金币</a>
								</li>
							</ul>
						</li>
						<li class="profile-sidebar-section">
							<h2 class="profile-sidebar-sectiontitle">
								<i class="icon-line-profile"></i>
								我的资料
							</h2>
							<ul>
								<li ng-class="{ active: pageName === 'info' }">
									<a href="/profile/info">个人资料</a>
								</li>
								<li ng-class="{ active: pageName === 'address' }">
									<a href="/profile/address">地址管理</a>
								</li>
							</ul>
						</li>
						<li class="profile-sidebar-section">
							<h2 class="profile-sidebar-sectiontitle" ng-class="{ active: pageName === 'favor' }">
								<a href="/profile/favor">
									<i class="icon-order-favor"></i>
									我的收藏
								</a>
							</h2>
						</li>
				  </ul>
				  <div class="profile-panel" role="main">
				  	<!-- ngIf: pageTitleVisible -->
				  	<h3 ng-if="pageTitleVisible" class="profile-paneltitle ng-scope">
				  		<span ng-bind="pageTitle" class="ng-binding">近三个月订单</span> 
				  		<span class="subtitle ng-binding" ng-bind-html="pageSubtitle | toTrusted"></span>
				  	</h3><!-- end ngIf: pageTitleVisible -->
				  	<div class="profile-panelcontent" ng-transclude="">
				  		<div class="order-fetchtakeout ng-scope ng-isolate-scope" show-fetch-takeout-dialog="">
				  			<img src="https://shadow.elemecdn.com/faas/desktop/media/img/takeout.408a87.png"></div>
				  			<div class="order-extra ng-scope">
				  				<a href="/support/question/hotissue" target="_blank">热门问题</a> 
				  				<a href="https://static11.elemecdn.com/eleme/desktop/mobile/index.html" target="_blank">随时关注订单状态
				  				</a>
				  			</div>
				  		<!-- 	<div class="loading ng-binding ng-isolate-scope ng-hide" loading="" ng-show="!orderList">ngIf: type==='profile'
				  		<img ng-if="type==='profile'" src="https://shadow.elemecdn.com/faas/desktop/media/img/profile-loading.4984fa.gif" alt="正在加载" class="ng-scope">end ngIf: type==='profile' ngIf: type==='normal'正在载入数据...
				  		</div> -->
				  		<!-- 	<div class="order-nocontent nodata ng-isolate-scope" ng-show="orderList &amp;&amp; !orderList.length" nodatatip="" content="暂无记录，现在就去<a href='/place'>订餐</a>吧!">
				  			<p class="nodata-container ng-binding" ng-bind-html="content | toTrusted">暂无记录，现在就去<a href="/place">订餐</a>吧!</p>
				  		</div> -->
				  			<table class="order-list ng-scope ng-hide" ng-show="orderList.length">
				  				<thead>
				  					<tr>
				  						<th class="order-list-infoth">订单内容</th>
				  						<th>数量</th>
				  						<th>金额（元）</th>
				  					</tr>
				  				</thead>
				  				<tbody>
				  					<tr v-for="item in GetShopCar" class="content-box">
				  						<th class="order-list-infoth content">{{item.name}}</th>
				  						<th>{{item.count}}</th>
				  						<th>{{item.price}}</th>
				  					</tr>
				  				</tbody>
				  			</table>
				  			<div class="pages ng-scope">
				  				<div class="user">
				  					<p>姓名：{{user.userName}}</p>
				  					<p>电话：{{user.phonenumber}}</p>
				  					<p>地址：{{user.adr}}</p>
				  				</div>
				  				<span class="tota-price">总价：{{GetTotalPrice}}</span>
				  				<span class="tota-price">总数：{{GetTotalCount}}</span>
				  				<span ng-click="onOrderPageChange('next')">提交</span>
				  			</div>
				  		</div>
				  	</div>
			</div>
		</div>
	</div>
</template>
<script>
  import {mapGetters} from 'vuex'
  import '@/store/state'
	export default{
		data(){
			return{
					orderList:[],
					totalPrice:0,
					user:''
			}
		},
		methods:{
			showUsers(){
				this.user=JSON.parse(localStorage.getItem("User"))
			}
		},
		created(){
			this.showUsers()
			this.$store.dispatch('loadDataFromLocal')
		},
		computed:{...mapGetters(['GetShopCar','GetTotalPrice','GetTotalCount'])}
	}
</script>
<style scoped>
  th{
  	font-size: 16px;
  }
	.content-box{
		height: 40px;
		vertical-align: middle;
	}
	.content{
		text-align: left;
    padding-left: 26px;
	}
	.tota-price{
		border: 0px;
	}
	.pages{
		font-size: 20px;
		float: left;
		margin-left: 17px;
	}
	.user{
		margin: 9px;
	}
</style>


