<template>
  <div id="app">
    <!--头部-->
    <head-div></head-div>
     <!--命名视图（旁边）-->
    <router-view name="cart"></router-view>
    <router-view></router-view>
    <!--底部-->
    <foot-div></foot-div>
    <!--购物车-->
    <shop-car></shop-car>
  </div>
</template>
<script>
import HeadDiv from '@/components/headDiv/HeadDiv.vue'
import FootDiv from '@/components/footDiv/FootDiv.vue'
import SideBar from '@/components/sideBar/SideBar.vue'
import ShopCar from '@/components/homeContent/shopDetail/shopCar/ShopCar.vue'
export default {
  name: 'App',
  components:{
    HeadDiv:HeadDiv,
    FootDiv:FootDiv,
    SideBar:SideBar,
    ShopCar:ShopCar
  },
    created(){
      this.$store.dispatch('loadDataFromLocal')
    }
}
</script>
<style>
