<template>
	<div id='ShopClass'>
		<!--商家分类开始-->
			<div class="excavator container">
				<div class="excavator-filter ng-scope">
					<span class="excavator-filter-name">商家分类:</span>
					 <!--商家一级菜单-->
					<a class="excavator-filter-item ng-binding ng-scope" href="javascript:" v-for="(item,index) in shopClass"  @click="showShop(item,index,item.id)"  v-bind:class="{active:index==oneIndex}">
						<!-- 给每一项分类添加跳转 -->
						{{item.name}}
					</a>
				  <!--商家二级菜单-->
					<div ng-show="subCategories" class="excavator-filter-subbox" v-show="shopDiv">
							<a class="excavator-filter-item ng-binding ng-scope" href="javascript:" v-for="(item,index) in shopClassList"  @click="changClass(item,index,item.id)" v-bind:class="{focus:index==twoIndex}">
							{{item.name}}
							</a>
					</div>
					<div class="excavator-filter-subbox ng-hide" v-show="shopDiv"></div>
				</div>
			</div>
			<!--商家分类结束-->
	</div>
</template>
<script>
	export default{
		data(){
			return{
						//商家一二级菜单
						shopDiv:false,
						shopClass:[
							{
								name:'全部商家',
								id:1
							},
							{
								name:'美食',
								id:2,
								List:[
								{name:'全部',id:11},
								{name:'简餐便当',id:12},
								{name:'地方菜系',id:13},
								{name:'小吃炸串',id:14},
								{name:'面食粥点',id:15},
								{name:'香锅冒菜',id:16},
								{name:'汉堡披萨',id:17},
								{name:'日韩料理',id:18},
								{name:'轻食西餐',id:19}]},
							{
								name:'快餐便当',
								id:3,
								List:[
									{name:'全部',id:11},
									{name:'简餐',id:12},
									{name:'米粉面馆',id:13},
									{name:'麻辣烫',id:14},
									{name:'盖浇饭',id:15},
									{name:'香锅砂锅',id:16},
									{name:'饺子馄饨',id:17},
									{name:'包子粥店',id:18},
									{name:'汉堡',id:19},
									{name:'黄焖鸡米饭',id:20},
									{name:'烧腊饭',id:21},
									{name:'煲仔饭',id:22},
									{name:'生煎锅贴',id:23},
									{name:'咖喱饭',id:24}]},
							{
								name:'特色菜系',
								id:4,
								List:[
									{name:'全部',id:11},
									{name:'川湘菜',id:12},
									{name:'火锅烤鱼',id:13},
									{name:'其他菜系',id:14},
									{name:'海鲜',id:15},
									{name:'粤菜',id:16},
									{name:'新疆菜',id:17},
									{name:'西北菜',id:18},
									{name:'江浙菜',id:19},
									{name:'东北菜',id:20}]},
								{
									name:'异国料理',
									id:5
								},
								{
									name:'小吃夜宵',
									id:6},
								{
									name:'甜品饮品',
									id:7},
								{
									name:'果蔬生鲜',
									id:8},
								{
									name:'商店超市',
									id:9},
								{
									name:'鲜花绿植',
									id:10},
								{
									name:'医药健康',
									id:11},
								{
									name:'早餐',
									id:12},
								{
									name:'午餐',
									id:13},
								{
									name:'下午茶',
									id:14},
								{
									name:'晚餐',
									id:15},
								{
									name:'夜宵',
									id:16
								}],
						shopClassList:[],//接受里面的二级菜单数组
						oneIndex:0,//一级菜单下标
						twoIndex:0,//二级菜单下标
						showList:[2,11],//筛选
						temp:null//
			}
			
		},
		created(){
				this.$emit("showId",this.showList)//在调用之前先调用一次，勾子函数
		},
		methods:{
					//显示二级菜单以及样式
			  	showShop(item,index,id){
			  		this.shopDiv=true
			  		this.shopClassList=item.List
			  		this.oneIndex=index
			  		this.twoIndex=0
			  		this.showList=[id,11]
			  		this.temp=id
			  		this.$emit("showId",this.showList)
			  	},
			  	//改变选中二级菜单class样式
			  	changClass(item,index,id){
			  		this.twoIndex=index
			  		this.showList=[this.temp,id]
			  		this.$emit("showId",this.showList)
			  	}
		}

	}
</script>


